


























































import { Component, Vue, Prop } from "vue-property-decorator";
// import { getRoleList } from "../../api/system/index2";
import {
  post_role_addRoleToJobBatch,
  post_job_getList
  
} from "../../api/system/index";
// import { Form as ElForm } from "element-ui";
import PaginationMixin from "../../mixins/pagination";
@Component({
  components: {},
  mixins: [PaginationMixin],
})
export default class BatchOperationJob extends Vue {
  constructor() {
    super();
  }
  @Prop({ default: null }) data: any;
  dialogVisible = true;

  selectList: any = [];
  queryPageParameters: any = {
    key: null,
  };
  resPageInfo: any = {
    total: 0,
    list: [],
  };

  cancel() {
    this.$emit("cancel", false);
  }

  async finish() {
    if (this.selectList.length > 0) {
      let p = {
        jobIds: this.selectList.map((item: any) => {
          item.id;
        }),
        roleId: this.data.id,
      };
      console.log(p);
      const res = await post_role_addRoleToJobBatch(p);
      this.$message.success("操作成功");

      this.$emit("finish", res);
    } else {
      this.$message.warning("请先选择数据");
    }
  }

  async getListMixin() {
    this.resPageInfo = await post_job_getList(this.queryPageParameters);
  }
  async created() {
    this.getListMixin();
  }
  handleSelectionChange(val: any) {
    console.log(val);
    this.selectList = val || [];
  }
}
